/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
  color: #1e1e1e; }

p {
  font-size: 2rem; }

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%; }

.header {
  width: 100%;
  height: 8rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  transition: 0.5s; }
  .header.header.active {
    background-color: rgba(248, 241, 241, 0.9); }
  .header .nav {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .header .nav .logo {
      display: flex;
      align-items: flex-end;
      /* justify-content: space-between; */ }
      .header .nav .logo img {
        max-width: 7rem; }
      .header .nav .logo .logo-text {
        font-size: 4rem;
        font-weight: bold;
        color: #1e1e1e; }

.hamburger-menu {
  font-size: 2.6rem;
  color: #2d2c2c;
  cursor: pointer;
  position: relative;
  z-index: 1500; }
  .hamburger-menu .fa-times {
    display: none; }

.menu-open .hamburger-menu .fa-times {
  display: block; }

.menu-open .hamburger-menu .fa-bars {
  display: none; }

.nav-list {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(248, 241, 241, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1400;
  opacity: 0;
  transform: scale(0);
  transition: opacity 0.5s;
  padding: 0; }
  .nav-list .nav-item:not(:last-child) {
    margin-bottom: 0.5rem; }
  .nav-list .nav-link {
    display: block;
    color: #1e1e1e;
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 1rem 0; }

.menu-open .nav-list {
  opacity: 1;
  transform: scale(1); }

/* efecto */
.nav-list .nav-item {
  position: relative; }

.nav-list .nav-item::after {
  content: "";
  height: 3px;
  width: 0;
  position: absolute;
  background: #16c79a;
  left: 0;
  bottom: 0;
  transition: 0.5s; }

.nav-list .nav-item:hover::after {
  width: 100%; }

.hero-image {
  background-image: var(--hero-image);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: var(--hero-attachment); }

.hero-image-opacity {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: var(--hero-opacity-color); }

.hero-image-title {
  font-size: 7.5vw;
  color: var(--hero-text-color); }

