.main-heading {
  color: #fff;
  line-height: 1;
  text-shadow: 1px 1px 1rem rgba(0, 0, 0, 0.5);
  margin-bottom: 2.5rem; }

.title {
  font-size: 4.8rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #fff; }

.title span {
  margin: 0;
  padding: 0;
  position: relative;
  font-size: 4.8rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: rgba(248, 241, 241, 0.5);
  -webkit-text-stroke: 0.2vw #00c4a1;
  display: inline-block; }

.title span::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  color: #11998e;
  -webkit-text-stroke: 0vw #00c4a1;
  border-right: 2px solid #00c4a1;
  overflow: hidden;
  animation: animate 6s linear infinite; }

@keyframes animate {
  0%,
  10%,
  100% {
    width: 0; }
  70%,
  90% {
    width: 100%; } }

.subtitle {
  font-size: 3.6rem;
  font-weight: 300;
  font-family: "Courgette", cursive; }

.project-grid p {
  display: grid;
  grid-template-columns: 1fr;
  line-height: 1.3;
  margin-bottom: 1.3rem; }

.project-img img {
  /* height: 100%; */
  width: 100%;
  /* background: url("./assets/images/img-1.jpg") center no-repeat; */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  margin-top: 5rem; }

.exam {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.exam .box {
  width: 32rem;
  position: relative;
  background-color: #fff;
  padding: 10rem 4rem 6rem;
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.1);
  margin: 1.5rem 0; }

.exam .box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--purple-light);
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.5s; }

.exam .box:hover:before {
  transform: scaleY(1);
  transform-origin: bottom;
  transition: transform 0.5s; }

.box .box-number {
  position: absolute;
  left: 4rem;
  top: 6rem;
  font-size: 8rem;
  font-weight: 800;
  z-index: 1;
  opacity: 0.2;
  transition: 0.5s; }

.box:hover .box-number {
  opacity: 1;
  color: #fff;
  transform: translateY(-4rem); }

.box h5 {
  position: relative;
  font-size: 1.5em;
  z-index: 2;
  color: #333;
  transition: 0.5s; }

.box h5 {
  position: relative;
  font-size: 1.5em;
  z-index: 2;
  color: #333;
  transition: 0.5s; }

.box .box-text {
  position: relative;
  font-size: 1.8rem;
  z-index: 2;
  color: #333;
  transition: 0.5s; }

.box:hover h5,
.box:hover .box-text {
  color: #fff; }

.contact-card {
  margin: 1rem auto;
  padding: 1rem;
  width: 100%;
  height: 144px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }

.contact-card > svg {
  width: 2rem;
  height: 2rem;
  fill: var(--first-color); }

.contact-card > small {
  margin-top: -1rem; }

.contact-form {
  margin: 2rem auto;
  padding: 1rem;
  max-width: 800px; }

.contact-form > * {
  padding: 0.5rem;
  margin: 1rem auto;
  display: block;
  width: 100%; }

.contact-form input,
.contact-form textarea {
  padding: 1rem; }

.contact-form input {
  border: 0;
  border-bottom: thin solid #00a1bd; }

.contact-form textarea {
  border: thin solid #00a1bd;
  resize: none; }

.contact-form input[type="submit"] {
  margin-top: 0;
  cursor: pointer;
  transition: all 0.5s ease-out; }

.contact-form input[type="submit"]:hover {
  opacity: 0.75; }

.contact-form *::placeholder {
  color: var(--gray-dark-color); }

.contact-form-response {
  padding: 1rem;
  width: 400px;
  text-align: center;
  background-color: var(--white-color); }

.contact-form-response svg {
  margin-top: 2rem;
  width: 4rem;
  height: 4rem;
  fill: var(--first-color); }

.modal#gracias:target {
  opacity: 1;
  pointer-events: auto; }

/* ********** Modal ********** */
.modal {
  position: fixed;
  z-index: 999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--black-alpha-color);
  opacity: 0;
  pointer-events: none;
  transition: all 1s; }

.modal-content {
  position: relative; }

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem; }

.modal-close svg {
  width: 3rem;
  height: 3rem;
  fill: var(--first-color); }

.modal[id|="trabajo"]:target {
  opacity: 1;
  pointer-events: auto; }

/* ********** ProgressBar ********** */
progress {
  width: 100%;
  height: 1rem;
  background-color: var(--gray-color); }

progress::-webkit-progress-bar {
  background-color: var(--gray-color); }

progress::-webkit-progress-value {
  background-color: var(--gray-dark-color); }

progress::-moz-progress-bar {
  background-color: var(--gray-dark-color); }

.progress {
  margin: 1rem auto; }

.progress > p {
  margin: 0;
  display: flex;
  justify-content: space-between;
  font-size: 0.85rem; }

/******** Accordion Section start ********/
.accordion {
  /* max-width: 400px; */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: hidden;
  font-family: "Quicksand", sans-serif;
  background: #009578; }

.accordion__label,
.accordion__content {
  padding: 14px 20px; }

.accordion__label {
  display: block;
  color: #ffffff;
  font-weight: 500;
  cursor: pointer;
  position: relative;
  transition: background 0.1s; }

.accordion__label:hover {
  background: rgba(0, 0, 0, 0.1); }

.accordion__label::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  width: 12px;
  height: 6px;
  background-image: url('data:image/svg+xml;utf8,<svg width="100" height="50" xmlns="http://www.w3.org/2000/svg"><polygon points="0,0 100,0 50,50" style="fill:%23FFFFFF99;" /></svg>');
  background-size: contain;
  transition: transform 0.4s; }

.accordion__content {
  background: #ffffff;
  line-height: 1.6;
  font-size: 0.85em;
  display: none; }

.accordion__input {
  display: none; }

.accordion__input:checked ~ .accordion__content {
  display: block; }

.accordion__input:checked ~ .accordion__label::after {
  transform: translateY(-50%) rotate(0.5turn); }

/******** Accordion Section start ********/
.footer {
  padding: 5rem 0 0 0;
  background-color: var(--purple-solid);
  color: #2d2c2c; }

.footer-content-brand {
  margin-bottom: 5rem; }

.footer .paragraph {
  color: inherit; }

.footer-heading {
  text-transform: uppercase;
  margin-bottom: 2.5rem; }

.social-media {
  display: flex; }

.sm-link {
  width: 4rem;
  height: 4rem;
  font-size: 2rem;
  border: 2px solid var(--yellow-solid);
  border-radius: 50px;
  display: flex;
  margin-right: 1rem;
  transition: background-color 0.5s; }

.sm-link:hover {
  background-color: var(--yellow-solid);
  color: var(--dark-color); }

.sm-link i {
  margin: auto; }

.social-media a {
  padding: 0 0.4rem;
  text-decoration: none; }

.copyright {
  color: #f3f3f3;
  background-color: #2f4858;
  height: 6rem;
  text-align: center;
  margin-top: 5rem;
  font-size: 1.3rem;
  display: flex;
  justify-content: center;
  align-items: center; }
